.site-content{
  min-height: 100dvh;
}
*{
  padding: 0;
  margin: 0;
}

/*CV*/
h1 {
  font-size: 24px;
  margin-bottom: 10px;
}
li {
  margin-bottom: 20px;
}
h3 {
  font-size: 20px;
  margin-bottom: 5px;
  color: #2179d8;
}
.utbildningsName {
  font-size: 20px;
  color: #2179d8; 
}
.cvName{
  display: flex;
  justify-content: center;
}

/*Min påskägg för 1337*/
.påskägg-container {
  text-align: center;
  margin-top: 50px;
}

.påskägg {
  font-size: 24px;
  color: #ff69b4; 
}

.beskrivning {
  font-size: 18px;
  color: #696969; 
}

/*Min välkommen sida*/


.pashi {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Hela fönstrets höjd */
  background-color: #f0f0f0; /* Bakgrundsfärg för hela sidan */
}

.bild {
  width: 300px; /* Bredden på bilden, justera efter behov */
  height: auto; /* Automatisk höjd för att behålla proportionerna */
}

.välkommen, .nourozi {
  text-align: center; /* Centrera texten horisontellt */
  margin-top: 20px; /* Lite mellanrum över och under texten */
}

 /* kontakt */
.contact {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  margin-top: 50px;
}

.contact h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #555;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.form-group textarea {
  height: 100px;
}

button[type="submit"] {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}



/* Om mig */
.om-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center; /* Centrera innehållet horisontellt */
}

.rubrik {
  font-size: 24px;
  margin-bottom: 20px;
}

.om-content {
  line-height: 1.6;
}

.rubrik1,
.rubrik2,
.rubrik3 {
  margin-bottom: 20px;
}

.halsa,
.zia {
  font-style: italic;
  margin-top: 20px;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

/* Min GITHUB projekt */
.projket-rubrik{
  font-size: 24px;
  margin-bottom: 20px;
  color: #0056b3;
  display: flex;
  justify-content: center;
}

.repoName {
  font-size: 20px;
  color: #333;
  margin-bottom: 5px;
  display: flex;
  justify-content: center;
}
.repoDescription{
  display: flex;
  justify-content: center;
}
.repoLänker{
  display: flex;
  justify-content: center;
}
